.container_board {
  padding-top: 26px;
  padding-left: 32px;
}
.container_prog > p {
  font-weight: 400;
  font-size: 25px;
  color: #505050;
  margin-bottom: 16px;
}

.container_prog_list {
  box-shadow: 5px 5px 15px 0 rgba(100, 100, 100, 0.12);
  background: #6987d3;
  border-radius: 20px;
  width: 680px;
  height: 131px;
  padding: 20px 25px;
}

.progg_list {
  display: flex;
  gap: 18px;
  margin: 0 auto;
}
.progg_list li {
  border-right: 2px solid #fdfdfd;
  padding-right: 18px;
}
.progg_list li > p {
  margin-top: 10px;
  margin-left: 18px;
}
.progg_list li:nth-child(4) {
  border-right: none;
}
.progg_list div {
  margin-top: 7px;
  display: flex;
  align-items: center;
}
.progg_list svg {
  margin-right: 14px;
}
.progg_list span {
  font-weight: 400;
  font-size: 40px;
  color: #fdfdfd;
}
.progg_list p {
  color: #fdfdfd;
}

/* next container */
.container_zax {
  margin-top: 35px;
  margin-bottom: 36px;
}

.container_zax > p {
  font-weight: 400;
  font-size: 25px;
  color: #505050;
  margin-bottom: 16px;
}
.zax_list {
  display: flex;
  padding-left: 52px;
  padding-top: 16px;
  padding-bottom: 10px;
  flex-direction: column;
  gap: 26px;
  box-shadow: 5px 5px 15px 0 rgba(100, 100, 100, 0.12);
  background: #f1f1f1;
  border-radius: 20px;
  width: 680px;
  height: 230px;
}
.zax_list li {
  display: flex;
}
.zax_list img {
  margin-right: 40px;
}
.zax_list button {
  margin-bottom: 10px;
  align-self: flex-end;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  cursor: pointer;
  padding: 10px;
  width: 139px;
  height: 37px;
  background: #63b048;
  font-weight: 400;
  font-size: 14px;
  color: #fff;
}
.zax_list_text {
  font-weight: 600;
  font-size: 18px;
  color: #505050;
  margin-bottom: 10px;
}
.zax_list_text + p {
  font-weight: 400;
  font-size: 16px;
  color: #939393;
}

/* container teachers*/
.container_second{
  padding-top: 26px;
padding-left: 68px;
}

.container_teachers > p{
  font-weight: 400;
font-size: 25px;
color: #505050;
margin-bottom: 16px;
}
.container_teachers img{
  border-radius: 100%;
  margin-right: 20px;
}
.teachers_list {
display: flex;

flex-direction: column;
border-radius: 20px;
padding: 17px 17px;
width: 280px;
box-shadow: 5px 5px 15px 0 rgba(100, 100, 100, 0.12);
background: #f1f1f1;
}
.teachers_list li{
  display: flex;
  cursor: pointer;
  align-items: center;
  padding-top: 17px;
  padding-bottom: 17px;
  border-bottom: 1px solid #505050;

}
.teachers_list li:nth-child(3){
  border: none;
}
.name_teacher{
  font-weight: 500;
font-size: 18px;
color: #505050;
}
.name_teacher + p{
  font-weight: 400;
font-size: 14px;
color: #505050;
}

/* chat container*/
.container_chat{
  margin-top: 52px;
}
.container_chat img{
  border-radius: 100%;
  margin-right: 25px;
}

.container_chat > p{
  font-weight: 400;
  font-size: 25px;
  color: #505050;
  margin-bottom: 16px;
}
.chat_list{
  border-radius: 20px;
width: 280px;
padding: 17px 15px;
box-shadow: 5px 5px 15px 0 rgba(100, 100, 100, 0.12);
background: #f1f1f1;
}
.chat_list li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.chat_name{
  font-weight: 600;
font-size: 18px;
color: #505050;
}
.chat_name + p{
  font-weight: 400;
font-size: 14px;
color: #bebebe;
}